<template>
  <div id="video-play">
    <video-player  class="video-player vjs-custom-skin"
                   ref="videoPlayer"
                   :playsinline="true"
                   :options="playerOptions"
    ></video-player>
  </div>
</template>

<script>
    export default {
      name:"videoPlay",
      data(){
        return{
          playerOptions : {
            playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
            autoplay: false, //如果true,浏览器准备好时开始回放。
            muted: false, // 默认情况下将会消除任何音频。
            loop: false, // 导致视频一结束就重新开始。
            preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
            language: 'zh-CN',
            aspectRatio: '18:11', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
            fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
            sources: [{
              type: "video/mp4",
              src: '' //url地址
            }],
            poster: '', //视频封面地址
            width: document.documentElement.clientWidth,
            notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
            controlBar: {
              timeDivider: true,
              durationDisplay: true,
              remainingTimeDisplay: false,
              fullscreenToggle: true  //全屏按钮
            }
          }

        }
      },

      watch:{
        async $route(to, from){
          if(to.query.id !== from.query.id){
            let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/play?id=' + to.query.id);
            this.playerOptions.poster = this.getImgURL(res.result.video.showImageFile.thumbnailPelativePath); // 视频封面图片
            this.playerOptions.sources[0].src = res.result.video.url; // 视频链接
          }
        }
      },

      async created(){
        let that = this;
        let id = this.$route.query.id;
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/play?id=' + id);
        that.playerOptions.poster = this.getImgURL(res.result.video.showImageFile.thumbnailPelativePath); // 视频封面图片
        that.playerOptions.sources[0].src = res.result.video.url; // 视频链接
      }
    }
</script>

<style scoped lang="scss">

</style>



